@tailwind base;
@tailwind components;
@tailwind utilities;


body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.slider-container {
  width: 80%;
  margin: 50px auto;
  text-align: center;
  

}

/* Style the slider track */
.slider {
  -webkit-appearance: none; /* Remove default WebKit appearance */
  appearance: none;
  width: 100%;
  height: 15px;
  background: var(--SliderBackgroundColor);
  border-radius: 10px;
  outline: none;
  transition: background 0.3s;
  border:solid;
  border-width: 2px;
  border-color: rgb(12, 12, 12);
}

/* Webkit browsers (Chrome, Safari) */
.slider::-webkit-slider-thumb {
  width: 28px !important;
  height: 28px !important;
  -webkit-appearance: none !important;
  appearance: none !important;
  background-color: var(--SliderColor) !important;
  border: 2px solid black !important;

}

/* Firefox */
.slider::-moz-range-thumb {
  width: 28px !important;
  height: 28px !important;
  -webkit-appearance: none !important;
  appearance: none !important;
  background-color: var(--SliderColor) !important;
  border: 2px solid black !important;
}

/* Internet Explorer */
.slider::-ms-thumb {
  width: 28px !important;
  height: 28px !important;
  -webkit-appearance: none !important;
  appearance: none !important;
  background-color: var(--SliderColor) !important;
  border: 2px solid black !important;
}

.slider-container {
  display: flex !important;
  flex-direction: column !important;
  align-items: center !important;
  max-width: 800px !important; /* Limits width for larger screens */
  margin: 0 auto !important; /* Centers the container */


}

@media (max-width: 480px) {
  .slider-container {
      width: 50%; /* Full width on very small screens */
  }
}